.floating-input-view {
  position: relative;
  width: 30%;
  height: 40px;
  border-bottom: 1px solid #d1d1d1;

  .dropdown-label {
    position: relative;
    font-size: 12px;
    color: #73787b;
    width: 100%;
    user-select: none;
  }

  select {
    position: absolute;
    right: 0;
    bottom: 4px;
    user-select: none;
    border: none;
    width: 100%;
    background: transparent;
    z-index: 1;
    -webkit-appearance: none;
    font-size: 12px;
    // font-weight: 600;
    &:focus {
      outline: 0;
    }
  }

  .dropdown-caret {
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: 0;
  }
}

.flip-up {
  transform: rotate(180deg);
}

.month, .day {
  width: 80px;
}

.year {
  width: 140px;
}

.right {
  margin-right: 0;
}